.career-container {
  max-height: 300px;
  /* 원하는 최대 높이로 설정 */
  overflow-y: auto;
  color: #6f42c1;
  background-color: #f8f9fa;
  padding: 20px;
}

.keyword {
  color: #007bff;
}

.experience {
  margin-top: 20px;
}

/* GitHub 테마에서 가져온 스타일 */
.markdown-body .highlight pre {
  background-color: #f6f8fa;
  border-radius: 6px;
  font-size: 85%;
  line-height: 1.45;
  overflow: auto;
  padding: 16px;
}

.markdown-body code {
  background-color: #f6f8fa;
  border-radius: 3px;
  font-size: 85%;
  margin: 0;
  padding: 0.2em 0.4em;
}